import React from 'react';
import styled from 'styled-components';
import { variables } from '@splunk/themes';
import LastUpdateTime from './LastUpdateTime';
import type { LastUpdateTimeProps } from './LastUpdateTime';

type alignment = 'left' | 'right';

interface ContainerProps {
    align: alignment;
    itemId: string;
    top?: string;
    bottom?: string;
}

const Container = styled.div.attrs<ContainerProps>(({ itemId }) => ({
    /**
     * As a child of a viz, data-id is probably not needed for tests as the viz already has that,
     * and the action menu should be a child of that ancestor.
     * It is left here for consistency with previous implementation
     */
    'data-id': itemId,
    'data-test': 'action-menus',
}))<ContainerProps>`
    display: inline-flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-shrink: 1;
    align-items: center;
    border-radius: 6px;
    padding: 3px;
    background-color: ${variables.backgroundColorPopup};
    color: ${variables.contentColorDefault};
    box-shadow: ${variables.overlayShadow};
    /* position relative to ancestor. ancestor must have position: relative or position: absolute */
    position: absolute;
    right: ${(props) => (props.align === 'right' ? 0 : 'auto')};
    left: ${(props) => (props.align === 'left' ? 0 : 'auto')};
    /* set to just below parent OR in bottom right corner of viz based on viz position in canvas */
    top: ${(props) => (props.top ? props.top : 'inherit')};
    bottom: ${(props) => (props.bottom ? props.bottom : 'inherit')};
    /* set to 2 to render action menus above visualization outline */
    z-index: 2;

    /* hide me when there are no child elements (no buttons or time displayed) */
    &:empty {
        display: none;
    }

    /**
     * The previous container implementation adjusted the background color of child buttons on hover,
     * but that should be the responsibility of the buttons themselves not the parent
     */
`;

export interface ActionMenuContainerProps extends LastUpdateTimeProps {
    actionButtons?: JSX.Element[];
    showLastUpdated: boolean;
    align?: alignment;
    itemId: string;
    /**
     * We probably don't really need action menu container to be responsible for
     * action buttons or memoizing them. These could be provided as children instead.
     * We will support both for now, until we figure out what we want to do.
     */
    children?: React.ReactNode;
    // To determine whether or not to render action menu outside or inside viz
    top?: string;
    bottom?: string;
}

/* To stop propagation on clicking on action menus to avoid executing the mouse events at the canvas level. */
const handleClick = (e: React.MouseEvent) => {
    e.stopPropagation();
};

const ActionMenuContainer = ({
    actionButtons,
    lastUpdated,
    status,
    isRealTimeSearch,
    showLastUpdated,
    align = 'right',
    itemId,
    children,
    top,
    bottom,
}: ActionMenuContainerProps): JSX.Element => {
    return (
        <Container
            align={align}
            itemId={itemId}
            onMouseDown={handleClick}
            onClick={handleClick}
            onMouseUp={handleClick}
            top={top}
            bottom={bottom}
        >
            {actionButtons}
            {children}
            {showLastUpdated && (
                <LastUpdateTime
                    status={status}
                    lastUpdated={lastUpdated}
                    isRealTimeSearch={isRealTimeSearch}
                />
            )}
        </Container>
    );
};

export default ActionMenuContainer;
